import yuanquYoushi from './components/yuanquyoushi.vue'
import aboutUs from  './components/aboutUs.vue'
import shouYe from  './components/shouye.vue'
import ruZhu from  './components/ruzhu.vue'
import goodPro from  './components/goodPro.vue'
import changJing from  './components/changjing.vue'
export default {
    name: "",
    components: {yuanquYoushi,aboutUs,shouYe,ruZhu,goodPro,changJing},
    computed: {},
    data() {
        return {
            initialIndex:0
        }
    },
    async asyncData() {

    },
    created() {

    },
    mounted(){
        //监听鼠标滚动事件
        if(process.browser){
            window.addEventListener('mousewheel',this.handleScroll);
        }
        if(this.$route.query.id){
            let index= this.$route.query.id

            this.$nextTick(() => {
                if(this.$refs.carousel)
                this.$refs.carousel.setActiveItem(index-1)
            })

        }
    },
    methods: {
        setActiveItem(index) {  //index为走马灯当前页码
            let nextIndex = index ++;
            console.log(nextIndex,'as');
            this.$refs.carousel.setActiveItem(nextIndex)
        },
            //判断滚动方向，因为此demo中只有四页，故边界处理为 0 与 3
            handleScroll(e){
            this.$route.query.id = null
                let direction = e.deltaY > 0 ? 'down':'up';  //deltaY为正则滚轮向下，为负滚轮向上
                if(direction=='down' && e.deltaY >= 100){ //125为用户一次滚动鼠标的wheelDelta的值
                    if(this.initialIndex >= 5){
                        this.initialIndex = 5
                    }else{
                        this.initialIndex += 1;
                        this.setActiveItem(this.initialIndex)
                    }
                }

                if(direction=='up' && e.deltaY <= -100){
                    if(this.initialIndex <= 0){
                        this.initialIndex = 0;
                        this.setActiveItem(0)
                    }else{
                        this.initialIndex -= 1;
                        this.setActiveItem(this.initialIndex)
                    }
                }

            },

    },
    watch: {}
}
